/* Setting the background color of the whole page to white */
.auth-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: white; /* Change background to white */
    padding: 0 20px;
    position: relative; /* Make sure the particles appear over the container */
  }
  
  /* The main card styles */
  .auth-card {
    width: 380px;
    padding: 40px;
    background-color: white;
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    border: 6px solid transparent; /* Transparent border for animation effect */
    margin-left: 20px;  /* Added margin to give space between card and image */
  }
  
  /* Create the moving border effect */
  .auth-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 6px solid red;
    border-radius: 10px;
    background: transparent;
    z-index: -1;
    animation: movingBorder 4s infinite linear;
  }
  
  @keyframes movingBorder {
    0% {
      border-top: 6px solid red;
      border-right: 6px solid transparent;
      border-bottom: 6px solid transparent;
      border-left: 6px solid transparent;
    }
    25% {
      border-top: 6px solid red;
      border-right: 6px solid red;
      border-bottom: 6px solid transparent;
      border-left: 6px solid transparent;
    }
    50% {
      border-top: 6px solid red;
      border-right: 6px solid red;
      border-bottom: 6px solid red;
      border-left: 6px solid transparent;
    }
    75% {
      border-top: 6px solid red;
      border-right: 6px solid red;
      border-bottom: 6px solid red;
      border-left: 6px solid red;
    }
    100% {
      border-top: 6px solid transparent;
      border-right: 6px solid red;
      border-bottom: 6px solid red;
      border-left: 6px solid red;
    }
  }
  
  /* Signin styles */
  .auth-title {
    font-size: 28px;
    font-weight: bold;
    color: #333;
  }
  
  .auth-form {
    display: flex;
    flex-direction: column;
  }
  
  .auth-input {
    margin: 10px 0;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
    transition: border-color 0.3s ease;
  }
  
  .auth-input:focus {
    border-color: #4caf50;
  }
  
  .auth-button {
    margin-top: 20px;
    padding: 12px;
    border: none;
    background-color: #4caf50;
    color: white;
    font-size: 16px;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .auth-button:hover {
    background-color: #45a049;
  }
  
  .switch-form {
    margin-top: 15px;
  }
  
  .auth-link {
    color: #007bff;
    text-decoration: none;
  }
  
  .auth-link:hover {
    text-decoration: underline;
  }
  
  /* Styling the image on the left side */
  .auth-image {
    width: 400px;
    height: auto;
    object-fit: cover;
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%); /* Vertically center the image */
    border-radius: 10px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Create a smooth moving gradient background */
  .auth-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(45deg, #ff6ec7, #f3a183, #f6b93b);
    background-size: 400% 400%; /* Set the size for animation */
    animation: gradientAnimation 10s ease infinite;
    padding: 0 20px;
  }
  
  /* Animation for gradient */
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  /* Floating particles animation */
  @keyframes particles {
    0% {
      transform: translateY(0);
      opacity: 1;
    }
    50% {
      transform: translateY(-20px);
      opacity: 0.7;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* Floating circles */
  .particle {
    position: absolute;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.8);
    opacity: 0.5;
    animation: particles 2s infinite ease-in-out; /* Faster animation */
    pointer-events: none;
  }
  
  /* Different sizes and animation durations for particles */
  .particle-1 {
    width: 12px;
    height: 12px;
    animation-duration: 2s;
    animation-delay: 0.5s;
  }
  
  .particle-2 {
    width: 15px;
    height: 15px;
    animation-duration: 2.5s;
    animation-delay: 0.2s;
  }
  
  .particle-3 {
    width: 10px;
    height: 10px;
    animation-duration: 2.8s;
    animation-delay: 0.7s;
  }
  
  .particle-4 {
    width: 20px;
    height: 20px;
    animation-duration: 3s;
    animation-delay: 1s;
  }
  
  .particle-5 {
    width: 18px;
    height: 18px;
    animation-duration: 2.2s;
    animation-delay: 1.5s;
  }
  
  .particle-6 {
    width: 8px;
    height: 8px;
    animation-duration: 2.7s;
    animation-delay: 0.4s;
  }
  
  /* Add more particles */
  .particle-7 {
    width: 14px;
    height: 14px;
    animation-duration: 3.3s;
    animation-delay: 0.8s;
  }
  
  .particle-8 {
    width: 16px;
    height: 16px;
    animation-duration: 2.4s;
    animation-delay: 1.1s;
  }
  