/* signup.css */

.auth-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to right, #6a11cb, #2575fc);
    position: relative; /* Position relative to allow particles to be positioned absolutely */
}

/* Particle animations */
@keyframes particles {
    0% {
      transform: translateY(0);
      opacity: 1;
    }
    50% {
      transform: translateY(-20px);
      opacity: 0.7;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
}

/* Floating circles */
.particle {
    position: absolute;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.8);
    opacity: 0.5;
    animation: particles 3s infinite ease-in-out;
    pointer-events: none;
}

.particle-1 {
    width: 12px;
    height: 12px;
    animation-duration: 4s;
    animation-delay: 1s;
}

.particle-2 {
    width: 10px;
    height: 10px;
    animation-duration: 5s;
    animation-delay: 0.5s;
}

.particle-3 {
    width: 8px;
    height: 8px;
    animation-duration: 6s;
    animation-delay: 2s;
}

.particle-4 {
    width: 14px;
    height: 14px;
    animation-duration: 7s;
    animation-delay: 3s;
}

.particle-5 {
    width: 16px;
    height: 16px;
    animation-duration: 5s;
    animation-delay: 1.5s;
}

.particle-6 {
    width: 10px;
    height: 10px;
    animation-duration: 6s;
    animation-delay: 2.5s;
}

/* Card Styling */
.auth-card {
    width: 420px;
    padding: 40px;
    background-color: white;
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    text-align: center;
    animation: flipIn 0.5s ease-out;
}

.auth-title {
    font-size: 28px;
    font-weight: bold;
    color: #333;
}

.auth-form {
    display: flex;
    flex-direction: column;
}

.auth-input {
    margin: 10px 0;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
    transition: border-color 0.3s ease;
}

.auth-input:focus {
    border-color: #4caf50;
}

.auth-button {
    margin-top: 20px;
    padding: 12px;
    border: none;
    background-color: #4caf50;
    color: white;
    font-size: 16px;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}

.auth-button:hover {
    background-color: #45a049;
}

.switch-form {
    margin-top: 15px;
}

.auth-link {
    color: #007bff;
    text-decoration: none;
}

.auth-link:hover {
    text-decoration: underline;
}
